import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { Button, TextField, Container, Grid, Typography } from "@mui/material";
import Navbar from "./Navbar";
import LoginForm from "./LoginForm";
import RegistrationForm from "./Registrationform";
import AdminPage from "./AdminPage";
import Welcome from "./welcome";
import WelcomeCotton from "./welcomecotton";
import { ThemeProvider } from "@mui/material/styles";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Switch,
} from "react-router-dom";
import axios from "axios";
import "./App.css";
import theme from "./theme";

function App() {
  const [formData, setFormData] = useState({
    cropType: "",
    teamName: "",
    password: "",
    captainFirstName: "",
    captainLastName: "",
    address: "",
    addressLine2: "",
    city: "",
    state: "",
    zipCode: "",
    country: "",
    email: "",
    phone: "",
    teamMembers: [],
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleTeamMemberChange = (index, field, value) => {
    const updatedTeamMembers = [...formData.teamMembers];
    updatedTeamMembers[index][field] = value;
    setFormData((prevData) => ({
      ...prevData,
      teamMembers: updatedTeamMembers,
    }));
  };

  const handleAddTeamMember = () => {
    setFormData((prevData) => ({
      ...prevData,
      teamMembers: [...prevData.teamMembers, { name: "", email: "" }],
    }));
  };

  const handleDeleteTeamMember = (index) => {
    const updatedTeamMembers = [...formData.teamMembers];
    updatedTeamMembers.splice(index, 1);
    setFormData((prevData) => ({
      ...prevData,
      teamMembers: updatedTeamMembers,
    }));
  };

  // const handleSubmit = (e) => {
  //   e.preventDefault();

  //   // Send form data to PHP script using Axios
  //   axios
  //     .post("http://localhost/step/register.php", formData) // Update the path to the register.php script
  //     .then((response) => {
  //       console.log("Response from server:", response.data);
  //       // Handle success or show a success message
  //     })
  //     .catch((error) => {
  //       console.error("Error sending data:", error);
  //       // Handle error or show an error message
  //     });
  // };

  const [registrationMessage, setRegistrationMessage] = useState("");
  const [redirecting, setRedirecting] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();

    // Send the form data to your Express API
    axios
      .post("http://localhost:3002/api/register", formData)
      .then((response) => {
        console.log("Response from server:", response.data);
        if (response.data.message === "Registration successful") {
          // Set the registration message
          setRegistrationMessage("Registration successful!");

          // Handle success or show a success message
          setTimeout(() => {
            setRedirecting(true);
            // Redirect to the login page after a delay
            window.location.href = "/login";
          }, 2500);
        }
      })
      .catch((error) => {
        console.error("Error sending data:", error);
        // Handle error or show an error message
      });
  };

  // ... (Your existing imports and code)

  return (
    <ThemeProvider theme={theme}>
      <Router>
        <div>
          <Navbar />

          <Routes>
            <Route path="/" element={<LoginForm />} />
            <Route
              path="/register"
              element={
                <RegistrationForm
                  formData={formData}
                  registrationMessage={registrationMessage}
                  handleChange={handleChange}
                  handleTeamMemberChange={handleTeamMemberChange}
                  handleAddTeamMember={handleAddTeamMember}
                  handleDeleteTeamMember={handleDeleteTeamMember}
                  handleSubmit={handleSubmit}
                  redirecting={redirecting}
                />
              }
            />
            <Route path="/login" element={<LoginForm />} />
            <Route path="/admin" element={<AdminPage />} />
            <Route path="/welcome" element={<Welcome />} />
            <Route path="/welcomecotton" element={<WelcomeCotton />} />
            {/* <Route path="/login" component={LoginForm} /> */}
          </Routes>
        </div>
      </Router>
    </ThemeProvider>
  );
}

export default App;
